:root {
  --pressed: black;
  --accent-gray: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  padding-left: 0;
  list-style-type: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header-link img {
  max-width: 100%;
  display: block;
}

.menu {
  margin: 0;
  display: flex;
}

.menu-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  color: var(--pressed);
  letter-spacing: .02em;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .nav-link:focus, .nav-link.is-active {
  color: var(--accent-gray);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--accent-gray);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu-item:first-child {
  margin-left: 0;
}

.menu-item:last-child {
  margin-right: 0;
}
