:root {
  --pressed: black;
  --accent-gray: #00acdc;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  list-style-type: none;
  padding-left: 0;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
}

.header-link img {
  display: block;
  max-width: 100%;
}

.menu {
  display: flex;
  margin: 0;
}

.menu-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  position: relative;
  color: var(--pressed);
  letter-spacing: 0.02em;
  font-weight: 500;
  height: 60px;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.nav-link:hover,
.nav-link:focus {
  color: var(--accent-gray);
}

.nav-link.is-active {
  color: var(--accent-gray);
}

.nav-link.is-active::after {
  position: absolute;
  left: 0;
  bottom: 0;
  content: '';
  width: 100%;
  height: 4px;
  background-color: var(--accent-gray);
  border-radius: 2px;
}

.menu-item:first-child {
  margin-left: 0;
}

.menu-item:last-child {
  margin-right: 0;
}
